import { createClient } from "@supabase/supabase-js";

// Function to create a Supabase client with environment variables
export const createSupabaseClient = (env?: any) => {
  const url = env?.SUPABASE_URL || "https://cihuylmusthumxpuexrl.supabase.co";
  const key =
    env?.SUPABASE_SERVICE_ROLE_KEY ||
    "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImNpaHV5bG11c3RodW14cHVleHJsIiwicm9sZSI6InNlcnZpY2Vfcm9sZSIsImlhdCI6MTc1MjU2OTExMSwiZXhwIjoyMDY4MTQ1MTExfQ.2sdXqL8HuvZris4IRzP0uG-yLyUIlgVyWwrKv6MpLTQ";

  if (!url || !key) {
    throw new Error("Supabase environment variables are not set");
  }

  return createClient(url, key, {
    auth: {
      autoRefreshToken: false,
      persistSession: false,
    },
  });
};

// Create a default client for Cloudflare Workers
export const supabase = createSupabaseClient();
